import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ReleveClientFiltrerComponent } from './releve-client-filtrer.component';

const routes: Routes = [
  {
    path: '',
    component: ReleveClientFiltrerComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReleveClientFiltrerRoutingModule { }
